import React from 'react'
import Aazhiyar from './Aazhiyar'
import BalloonFestival from './BalloonFestival'
import TopSlip from './TopSlip'
import Vaalparai from './Vaalparai'

function OutputWindow(props) {
    if (props.name=='Topslip'){
        return(
            <div>
                <TopSlip/>
            </div>
        )
    }
    if (props.name=='Aazhiyar'){
        return(
            <div>
                <Aazhiyar/>
            </div>
        )
    }
    if (props.name=='Balloon Festival'){
        return(
            <div>
                <BalloonFestival/>
            </div>
        )
    }
    if (props.name=='valparai'){
        return(
            <div>
                <Vaalparai/>
            </div>
        )
    }
    return(<div>
       <h1>{props.name}</h1> 
    </div>)
    
}

export default OutputWindow
